@mixin onTablet {
  @media (min-width: $tablet-min-width) {
    @content;
  }
}

@mixin onDesktop {
  @media (min-width: $desktop-min-width) {
    @content;
  }
}

@mixin contentPaddingInline() {
  margin: 0 20px;

  @include onDesktop() {
    max-width: 1020px;
    margin-inline: auto;
  }
}

.recommended,
.categories,
.contacts,
.description__text,
.menu__bottom,
.menu__top,
.header__content {
  @include contentPaddingInline;
}

@mixin hover($property, $toValue) {
  transition: #{$property} $effectDuration;
  &:hover {
    #{$property}: $toValue;
  }
}

@mixin pageGrid {
  --columns: 2;
  display: grid;
  column-gap: 20px;
  grid-template-columns: repeat(var(--columns), 1fr);

  @include onTablet {
    --columns: 6;
    column-gap: 24px;
  }

  @include onDesktop {
    --columns: 12;
    column-gap: 24px;
  }
}
