
.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #f9f9f9;
  font-weight: 700;

  &__top {
    margin-bottom: 92px;

    @include onTablet {
      margin-bottom: 0;
    }
  }

  &__nav {
    margin-bottom: 48px;
  }

  &__phone-number {
    letter-spacing: 2px;
    display: block;
    margin-bottom: 16px;
    color: inherit;
    text-decoration: none;
  }

  &__call-to-order {
    display: block;
    width: fit-content;
    padding-bottom: 4px;
    border-bottom: 1px solid $main-text-color;
    letter-spacing: 2px;
    color: inherit;
    text-decoration: none;
    text-transform: uppercase;
  }

  &__bottom {
    margin-top: 56px;
  }
}
